




:root {
  --primary-color: #EB7AA4;
  --primary-dark: #ff34d3;
  --secondary-color: #fd0782;
  --secondary-dark:#e30258;
  --overlay:rgba(0,0 ,0,.7);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  /* Replace with your desired font stack for body text */
  font-family: "Gotham Book", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.2rem;
  line-height: 1.2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.button {
  padding: 17px 28px;
  font-size: 1.2rem;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  background-color: var(--primary-color);
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  cursor: pointer;
}

.button:hover {
  background: var(--primary-dark);
  border: 1px solid var(--primary-dark);
}

.line {
  display: block;
  width: 40px;
  height: 3px;
  margin: 5px auto;
  background-color: var(--primary-color);
}
h1, h2, h3 {
  /* Replace with your desired font for headings */
  font-family: "Gobold extra1", serif;
}

/* Styles for Subheadings (h2, h3) */
h2, h3 {
  /* If you want a different font for some subheadings, use a separate selector */
  font-family: "Surimi", sans-serif;  /* Optional for different subheading font */
}